<template>
  <el-container>
    <!-- 表单弹窗 -->
    <userInfo-form ref="userInfoDialog" @reload="refreshUserInfo"/>
    <password-form ref="passwordDialog" @reloadList="getList"/>
    <el-aside class="el-aside" :class="[{'aside-hidden': isHidden,'aside-horizontal':mode=='horizontal'}]">
      <el-menu
        :mode="mode"
        class=""
        background-color="#333"
        text-color="#fff"
        active-text-color="#409EFF"
        :default-active="$route.path" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router>
        <el-submenu v-if="menu.children&&!menu.href" v-for="(menu,index) in menuList" :key="index"  :index="index">
          <template slot="title">
            <i :class="menu.icon"></i><span class="span" slot="title">{{menu.name}}</span>
          </template>
          <el-menu-item @click="addTab(item)" :index="item.href" v-for="(item,childrenIndex) in menu.children" :key="childrenIndex">
            <span class="span" slot="title">{{item.name}}</span>
          </el-menu-item>
        </el-submenu>
        <el-menu-item class="el-submenu__title" v-else @click="addTab(menu)" :index="menu.href">
          <i :class="menu.icon"></i><span slot="title" class="span">{{menu.name}}</span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container style="height: 100vh;" :class="[{'body-horizontal':mode=='horizontal'}]">
      <el-header class="el-header" :class="[{'header-horizontal':mode=='horizontal'}]">
        <div v-if="mode!='horizontal'" style="float: left" >
          <svg t="1637039383052" @click="collapse" :class="{'is-active':!isHidden}" class="icon hamburger" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3444" width="200" height="200"><path d="M935.27 12.34H89.78c-41.38 0-75.23 33.85-75.23 75.23S48.4 162.8 89.78 162.8h845.49c41.38 0 75.23-33.85 75.23-75.23 0-41.37-33.85-75.23-75.23-75.23zM935.27 295.17H496.64c-41.38 0-75.23 33.85-75.23 75.23s33.85 75.23 75.23 75.23h438.63c41.38 0 75.23-33.85 75.23-75.23 0-41.37-33.85-75.23-75.23-75.23zM935.27 578.27H496.64c-41.38 0-75.23 33.85-75.23 75.23s33.85 75.23 75.23 75.23h438.63c41.38 0 75.23-33.85 75.23-75.23 0-41.37-33.85-75.23-75.23-75.23zM935.27 861.64H89.78c-41.38 0-75.23 33.85-75.23 75.23s33.85 75.23 75.23 75.23h845.49c41.38 0 75.23-33.85 75.23-75.23 0-41.37-33.85-75.23-75.23-75.23zM26.95 544.85l235.2 176.67c25.2 18.93 61.19 0.95 61.19-30.57V332.96c0-31.74-36.44-49.65-61.57-30.28L26.57 484.01c-20.01 15.42-19.82 45.67 0.38 60.84z" p-id="3445" fill="#ffffff"></path></svg>
          <span class="project-name">{{projectName}}</span>
        </div>
        <el-dropdown>
          <i class="el-icon-setting user-info" >
            <span v-for="role in userInfo.roleList">【{{role.roleName}}】</span>
          </i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="openUserInfoDialog({title:'用户信息',pageFrom:'edit',id:userInfo.id})">用户信息</el-dropdown-item>
            <el-dropdown-item @click.native="openPasswordDialog({title:'修改密码'})">修改密码</el-dropdown-item>
            <el-dropdown-item @click.native="loginOut">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown>
          <el-image class="head-img div-shadow" :src="formatUrl(userInfo.photo)"></el-image>
          <span style="color: white;font-weight: bold;font-size: medium">{{userInfo.loginName}}</span>
          <el-dropdown-menu v-if="userInfo.office" slot="dropdown">
            <el-dropdown-item>所属机构:【{{userInfo.office.name}}】</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-tabs v-model="editableTabsValue" type="border-card" style="background: white;border: none" closable @tab-click="changeTab" @tab-remove="removeTab">
        <el-tab-pane v-for="(item, index) in editableTabs" :key="item.name" :label="item.title" :name="item.href">
        </el-tab-pane>
      </el-tabs>
      <el-main style="background: white">
        <router-view></router-view>
      </el-main>
      <el-footer class="copyright">Copyright © 2022 by {{projectName}}</el-footer>
    </el-container>
  </el-container>
</template>
<script>
  import userInfoForm from '../../module/user/userInfo'
  import passwordForm from '../../module/user/userPassword'
  import {mapActions} from "vuex";
  export default {
    components: {userInfoForm,passwordForm},
    data() {
      return {
        isHidden:false,
        isCollapse: false,
        editableTabsValue: '/home',
        editableTabs: [{
          title: '首页',
          href: '/home',
        }],
        menuList:[],
      }
    },
    mounted() {
      if (this.$route.path !== '/home') {
        this.changeRouter("/home");
      }
      this.$request.getMenuUserTree().then(res=>{
        if(res.isOk){
          this.menuList=res.list
        }else{
          const that = this;
          that.showConfirm("系统过期,请重新登录!",function () {
            that.$router.push('/login');
          })
        }
      })
    },
    computed:{
      mode(){
        return this.$store.getters.config.isHorizontal?'horizontal':'vertical';
      },
      userInfo(){
        return this.$store.getters.userInfo
      },
      projectName(){
        return this.$store.getters.config.projectName
      },
    },
    methods: {
      ...mapActions(['commitLoginOut','commitUserInfo']),
      refreshUserInfo(){
        this.commitUserInfo()
      },
      //点击查看
      openUserInfoDialog(option) {
        this.$refs.userInfoDialog.open(option);
      },
      //点击查看
      openPasswordDialog(option) {
        this.$refs.passwordDialog.open(option);
      },
      loginOut(){
        const that = this;
        that.showConfirm('确认退出吗?',function (){
          that.commitLoginOut().then(res=>{
            if(res.isOk){
              that.showToast("退出成功");
              that.$router.push('/login');
            }
          })
        })
      },
      collapse() {
        this.isCollapse=!this.isCollapse
        if(this.isCollapse){
          this.isHidden=true
        }else{
          this.isHidden=false
        }
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      changeRouter(targetHref){
        this.$router.push(targetHref); //跳转到指定页面
      },
      changeTab(menu){
        this.$router.push(menu.name); //跳转到指定页面
      },
      addTab(menu) {
        let tabs = this.editableTabs.map(function (v) {
          return v.href
        })
        let hasTab = tabs.contain(menu.href);
        if(!hasTab){
          this.editableTabs.push({
            title: menu.name,
            href: menu.href,
          });
        }
        this.editableTabsValue = menu.href;
      },
      removeTab(targetHref) {
        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetHref) {
          tabs.forEach((tab, index) => {
            if (tab.href === targetHref) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.href;
                this.changeRouter(nextTab.href)
              }
            }
          });
        }
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.href !== targetHref);
      }
    }
  };
</script>
<style scoped>
  .user-info{
    vertical-align:middle;
    color: white;
    font-weight: bold;
    font-size: medium;
    padding: 10px
  }
  .user-info:hover{
    border-radius: 5px;
    background: rgba(43, 74, 120, 0.21);
  }
  .head-img {
    vertical-align:middle;
    margin-right: 5px;
    width: 35px;
    height: 35px;
    border-radius: 50%
  }
  .span{
    margin-left: 5px;
  }
  .header-horizontal{
    position: absolute;top: -60px;right: 0;background: rgb(51, 51, 51)!important;
  }
  .body-horizontal{
    position: absolute;top: 60px;width: 100%;
  }
  .copyright {
    border-top: 1px solid #e7eaec;
    background: white;
    padding: 5px;
    height: 30px!important;
    text-align: center;
    font-size: smaller;
    color: grey;
  }
  .el-header {
    text-align: right;
    font-size: 12px;
    background: #3c8dbc;
    padding: 0 10px;
    line-height: 60px;
    height: 100%;
  }
  aside{
    padding: 0;
    background: rgb(238, 241, 246);
  }
  .el-aside {
    overflow: auto;
    height: 100vh;
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
    background: #304156;
    width: 250px!important;
  }
  .aside-horizontal{
    width: 100%!important;
  }
  .el-aside .el-menu{
    border-right: none;
  }
  .aside-hidden {
    transition: width 0.3s;
    -webkit-transition:width 0.3s;
    width: 64px!important;
  }
  .hamburger {
    color: white;
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
  }
  .hamburger:hover {
    padding: 0.5px;
    background: rgba(255, 255, 255, 0.11);
  }
  .hamburger.is-active {
    transform: rotate(180deg);
  }
  .project-name{
    padding-left: 5px;
    vertical-align: middle;
    font-size: 25px;
    color: white;
    font-weight: bold;
  }

</style>



